    function developerAPIQuery(authObj, paramObj) {
        let What = 'API';
        let queryObj = prepareMsg(What);
        let { app_key, app_secret, access_token } = authObj;
        let arr = [];
        arr[0] = app_secret;
        let timeStamp = new Date(new Date().valueOf() - new Date().getTimezoneOffset() * 60 * 1000)
            .toISOString()
            .replace(/T/, ' ')
            .replace(/\..*$/, '');
        let unordered = {
            app_key: app_key,
            format: 'json',
            session: access_token,
            sign_method: 'md5',
            timestamp: timeStamp,
            v: '2.0'
        };
        if (paramObj) Object.assign(unordered, paramObj); //
        let o = {}; //
        Object.keys(unordered)
            .sort()
            .forEach((k) => {
                o[k] = unordered[k]; //key
            });
        for (let k in o) {
            let str = '';
            if (o[k] instanceof File === false) {
                str = k + o[k];
            } //
            arr.push(str);
        }
        arr.push(app_secret);
        let md5Data = arr.join('');
        let sign = hex_md5(md5Data).toUpperCase(); //MD5
        Object.assign(o, { sign });
        queryObj.request.data = o;
        return queryObj;
    }
    function developerAPICheck(msg) {
        let data = msg.response.data;
        let status, result;
        if (data.error_response) {
            let { msg, sub_code, sub_msg } = data.error_response;
            result = msg;
            if (sub_code && sub_msg) result += ` (${sub_code}):\n${sub_msg}`;
            status = false;
        } else {
            result = data;
            status = true;
        }
        msg.response.result = result;
        msg.response.status = status;
        return msg;
    }

